        <ul>
          <router-link to="/list" tag="li">今日精选</router-link>
          <router-link to="/calendar" tag="li">日历</router-link>
        </ul>
      </nav>
    </header>
    <router-view></router-view>
  </div>
</template>

<script>

export default {
  name: 'app',

  components: {
    
  },

  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
