    this.$store.commit('SET_CALENDAR')
  }
}
</script>

<style lang="scss">

@import 'sass/mixin/flexbox.scss';

* {
  margin: 0;
  padding: 0;
}
html {
  font-size: 14px;
}
h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
  font-size: 1rem;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
@media (min-width: 800px) {
  #app {
    max-width: 600px;
    margin: 0 auto;
    height: 100%;
