}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.header {
  background: #46C1FD;
  color: #ffffff;
}
.header > h1 {
  box-sizing: border-box;
  padding: .65rem 0 0 0;
  font-size: 1.425rem;
}
nav.inner {
  box-sizing: border-box;
  padding: .325rem;
  font-size: 1.125rem;
}
nav.inner > ul {
  @include flexbox();
  @include flex-flow(row, nowrap);
}
nav.inner > ul > li {
  @include flex(1);
  box-sizing: border-box; 
  padding: .325rem;
  color: #ffffff;
}
</style>


